"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { authService, type TeamDetails } from "@/lib/auth-service"

export default function ConfirmTeamPage() {
  const router = useRouter()
  const [team, setTeam] = useState<TeamDetails | null>(null)
  const [loading, setLoading] = useState(true)
  const [confirming, setConfirming] = useState(false)
  const [error, setError] = useState("")

  useEffect(() => {
    const fetchTeamDetails = async () => {
      try {
        const teamData = await authService.getTeamDetails()
        setTeam(teamData)
      } catch (err: any) {
        setError(err.message || "Failed to load team details")
      } finally {
        setLoading(false)
      }
    }

    fetchTeamDetails()
  }, [])

  const handleConfirm = async () => {
    setConfirming(true)
    setError("")

    try {
      await authService.confirmTeamDetails()
      router.push("/dashboard")
    } catch (err: any) {
      setError(err.message || "Failed to confirm team details")
    } finally {
      setConfirming(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <Card className="p-8">
          <p className="text-gray-600">Loading team details...</p>
        </Card>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-white flex flex-col items-center justify-center px-4 py-8">
      <div className="max-w-2xl w-full">
        {/* Header */}
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold mb-2" style={{ color: "#f75700" }}>
            Confirm Team Details
          </h1>
          <p className="text-gray-600">Please verify your team information before proceeding</p>
        </div>

        {/* Team Details Card */}
        <Card className="p-8 bg-white border border-gray-200 shadow-sm">
          {error && <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded text-red-700">{error}</div>}

          {team && (
            <div className="space-y-6">
              {/* Team Info */}
              <div>
                <h2 className="text-xl font-semibold text-blue-700 mb-4">Team Information</h2>
                <div className="space-y-3 bg-blue-50 p-4 rounded border border-blue-200">
                  <div>
                    <p className="text-sm text-gray-600">Team Name</p>
                    <p className="font-semibold text-gray-900">{team.name}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600">Institution</p>
                    <p className="font-semibold text-gray-900">{team.institution}</p>
                  </div>
                  {team.problem_statement && (
                    <div>
                      <p className="text-sm text-gray-600">Problem Statement</p>
                      <p className="font-semibold text-gray-900">{team.problem_statement.title}</p>
                      <p className="text-xs text-gray-600">ID: {team.problem_statement.id}</p>
                    </div>
                  )}
                  {team.faculty_mentor && (
                    <div>
                      <p className="text-sm text-gray-600">Faculty Mentor</p>
                      <p className="font-semibold text-gray-900">{team.faculty_mentor.name}</p>
                    </div>
                  )}
                </div>
              </div>

              {/* Team Members */}
              <div>
                <h2 className="text-xl font-semibold text-blue-700 mb-4">Team Members</h2>
                <div className="space-y-2">
                  {team.members &&
                    team.members.map((member, idx) => (
                      <div key={idx} className="p-3 bg-gray-50 rounded border border-gray-200">
                        <div className="flex justify-between items-start">
                          <div>
                            <p className="font-semibold text-gray-900">{member.user.name}</p>
                            <p className="text-sm text-gray-600">{member.user.email}</p>
                          </div>
                          <span className="text-xs font-medium px-2 py-1 bg-blue-100 text-blue-700 rounded capitalize">
                            {member.role}
                          </span>
                        </div>
                      </div>
                    ))}
                </div>
              </div>

              {/* Confirmation */}
              <div className="pt-6 border-t border-gray-200 space-y-4">
                <div className="p-4 bg-green-50 border border-green-200 rounded">
                  <p className="text-sm text-green-700">
                    âœ“ Please review the information above and confirm that all details are correct.
                  </p>
                </div>

                <div className="flex gap-4">
                  <Button
                    onClick={handleConfirm}
                    disabled={confirming}
                    className="flex-1 bg-blue-600 hover:bg-blue-700 text-white py-3 font-semibold"
                  >
                    {confirming ? "Confirming..." : "Confirm & Continue"}
                  </Button>
                  <Button onClick={() => router.push("/")} variant="outline" className="flex-1 border-gray-300">
                    Go Back
                  </Button>
                </div>
              </div>
            </div>
          )}
        </Card>
      </div>
    </div>
  )
}
